const brushResizePath = height => {
  return d => {
    const e = +(d.type == 'e'),
      x = e ? 1 : -1,
      y = height / 2;

    return (
      'M' +
      0.5 * x +
      ',' +
      y +
      'A6,6 0 0 ' +
      e +
      ' ' +
      6.5 * x +
      ',' +
      (y + 6) +
      'V' +
      (2 * y - 6) +
      'A6,6 0 0 ' +
      e +
      ' ' +
      0.5 * x +
      ',' +
      2 * y +
      'Z' +
      'M' +
      2.5 * x +
      ',' +
      (y + 8) +
      'V' +
      (2 * y - 8) +
      'M' +
      4.5 * x +
      ',' +
      (y + 8) +
      'V' +
      (2 * y - 8)
    );
  };
};

export default brushResizePath;
