import { getTransparentColor } from 'vizart-core';

const highlightNode = (context, opt, c, x, y) => {
  context.save();

  context.save();
  context.beginPath();
  context.fillStyle = opt.plots.highlightNodeColor
    ? opt.plots.highlightNodeColor
    : getTransparentColor(c, 0.9);
  context.strokeStyle = 'white';
  context.lineWidth = 2;
  context.arc(x, y, 6, 2 * Math.PI, false);
  context.fill();
  context.stroke();

  context.closePath();

  context.restore();
};

export default highlightNode;
